            {{__('Exclude dark mode on HTML tags, CSS class, CSS ids and more', 'wp-dark-mode')}}</template>
        </SectionHead>
        <Row class="flex-col sm:flex-row w-full" space>
            
            <Heading class="whitespace-nowrap w-auto">
