export default ((config = {}) => {
    return `<div class="switch-inner">
            <span>${config.iconLight || wp_dark_mode_icons.LOWSUN}</span>
            <span>${config.iconDark || wp_dark_mode_icons.TILTMOON}</span>
            <span class="glass" tabindex="1"></span>
        </div>
       <svg style="display:none;">
        <filter id="glassEffect">
            <feImage href="data:image/png;base64," 
                    preserveAspectRatio="none" />
    
            <feDisplacementMap in="SourceGraphic"
                in2="turbulence"    
                            scale="200" xChannelSelector="R" yChannelSelector="G" />
        </filter>
    </svg>`;
   });